video {
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -1;
}

.hero-container {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #56ccf2; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #56ccf2, #2f80ed); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #56ccf2, #2f80ed);
  object-fit: contain;
}
.viac-info {
  font-size: 2rem;
  color: white;
  text-align: center;
  margin-top: 1em;
  width: 30em;
}
.poplatky{
  width: auto;

}
.viac-info-scroll {
  font-size: 0.8rem;
  color: white;
  text-align: center;
  margin-top: 1.1em;
  border-style: solid;
  padding: 1em;


}
.hero-container > h1 {
  color: #fff;
  font-size: 4rem;
  text-align: center;
}

.write-icon {
  color: black;
  margin-left: 0.3em;
}

.hero-container > p {
  text-align: center;
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
  .viac-info {
    font-size: 1.5rem;
    color: white;
    text-align: center;
    margin-top: 1em;
    width: 30em;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 45px;
    margin-top: -1em;
    margin-bottom: 0.3em;
    padding: 0 0.2em;
  }

  .hero-container > p {
    font-size: 22px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
  .viac-info {
    font-size: 1.2rem;
    color: white;
    text-align: center;
    margin-top: 1.5em;
    width: 30em;
    margin-left: 0.7em;
    margin-right: 0.7em;
  }
}
@media screen and (max-width: 510px) {
  .viac-info {
    font-size: 1rem;
    color: white;
    text-align: center;
    margin-top: 1.5em;
    width: auto;
    margin-left: 0.7em;
    margin-right: 0.7em;
  }
}
a { color: white; text-decoration: none; }

.arrow {
  text-align: center;
  margin-left: -1.2em;
  margin-right: -1.2em;
}
.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-15px);
  }
  60% {
    transform: translateY(-10px);
  }
}
.vedla {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 1em;
  cursor: pointer;
}