.onas-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}
.onas-container-info {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: rgb(36, 36, 36);
  height: 49vh;
  z-index: 1;
  color: white;
  font-size: 1.5rem;
  text-align: center;
}
ul >  li > a{
  font-size: 1rem;
}
.onas-text {
  width: 50%;
  padding: 1.5em;
  font-weight: 100;
  text-align: center;
  height: 49vh;
  display: flex;
  align-items: center;
}
.who-can {
  padding: 1.5em;
  width: 50%;
  text-align: left;
  font-weight: 100;
  padding-left: 5em;
}
.who-can > ul {
  margin-left: 1.5em;
  margin-top: 1em;
}
.image-text {
  background: transparent;
}
.onas-item-title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  height: 40vh;
  width: 50vw;
  background-color: rgb(21, 21, 21);
  font-size: 2rem;
  padding: 1em;
  text-align: center;
  font-weight: 100;
}
.onas-item-body {
  text-align: center;
  width: 50vw;
  height: 40vh;
  z-index: -1;
  position: relative;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  z-index: 1;
  color: white;
  width: 70%;
}
.onas-item-body > img {
  width: 100%;
  height: 19em;
  object-fit: cover;
  filter: brightness(20%);
}

.onas-nav-icon {
  margin-left: 0.3em;
  margin-bottom: 0.1em;
  font-size: 3rem;
}

@media screen and (max-width: 1000px) {
  .onas-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(36, 36, 36);
    height: 100%;
    z-index: 0;
    color: white;
    font-size: 1.5rem;
    text-align: center;
  }
  .onas-item-title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    height: 100%;
    width: auto;
    background-color: rgb(21, 21, 21);
    font-size: 1.5rem;
    padding: 1em;
    text-align: center;
    font-weight: 100;
    order: 1;
  }
  .onas-item-body {
    text-align: center;
    width: 100%;
    height: 10em;
    z-index: 0;
    order: 0;
  }
  .onas-nav-icon {
    margin-left: 0.3em;
    margin-bottom: 0.1em;
    font-size: 1.5rem;
  }
  .center {
    display: flex;
    justify-content: center;
    font-size: 1.5rem;
    color: white;
    width: 90%;
  }
  .onas-item-body > img {
    width: 100%;
    height: 10em;
    object-fit: cover;
    filter: brightness(20%);
  }
  .onas-container-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(36, 36, 36);
    height: 100%;
    z-index: 0;
    color: white;
    font-size: 1.5rem;
    text-align: center;
  }
  .onas-text {
    width: 100%;
    padding: 1.5em;
    font-weight: 100;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    font-size: 1rem;
    background-color: rgb(21, 21, 21);
  }
  .who-can {
    padding: 1.5em;
    width: 100%;
    text-align: left;
    font-weight: 100;
    padding-left: 3em;
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
