.card-home-section {
  background-color: white;
}
.card-home-title {
  text-align: center;
  padding: 1em;
  font-size: 2rem;
}
.card-container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.card-item {
  text-align: center;
  margin: 1em;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 5px;
}
.card-item > .title {
  font-size: 1rem;
  padding: 0.5em;
  margin-top: 0.5em;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-item > .body {
  font-size: 0.9rem;
  text-align: justify;
  padding: 1em;
}

.card-item > .image {
}
.card-item > .image > img {
  max-width: 100%;
  height: auto;
  border-radius: 5px 5px 0px 0px;
}
@media screen and (min-width: 700px) {
  .card-container {
    justify-content: space-around;
    flex-direction: row;
    margin: 0 1em;
  }
  .card-item > .title {
    font-size: 1.3rem;
  }
  .card-item > .body {
    font-size: 1rem;
    text-align: justify;
    padding: 1em;
  }
}
